body{background-color:#282D32;color:#EEF7FF;font-family:"Nunito";font-weight:800}.titleCard-card{background-color:#585EEA;height:60px;line-height:60px;border-radius:10px;box-shadow:-5px -5px 10px 0 rgba(0,0,0,0.16),5px 5px 10px 0 rgba(0,0,0,0.16)}.titleCard-title-text{vertical-align:middle;margin-left:17px;font-weight:800}

body{background-color:#282D32;color:#EEF7FF;font-family:"Nunito";font-weight:800}.card-card{background-color:#2D333A;height:50px;line-height:50px;border-radius:10px;box-shadow:-3px -3px 10px 0 rgba(0,0,0,0.12),3px 3px 10px 0 rgba(0,0,0,0.12)}.card-text{vertical-align:middle;margin-left:17px;font-weight:600}

body{background-color:#282D32;color:#EEF7FF;font-family:"Nunito";font-weight:800}.addnewcard-card{background-color:#282D32;height:60px;line-height:60px;border-radius:10px;border-width:2px;border-style:dashed;border-color:rgba(88,94,234,0.5);cursor:pointer}.addnewcard-text{vertical-align:middle;margin-left:17px;font-weight:600}

body{background-color:#282D32;color:#EEF7FF;font-family:"Nunito";font-weight:800}.searchgamecard-card{background-color:#2D333A;height:60px;line-height:60px;border-radius:10px;padding-top:2px;box-shadow:-3px -3px 10px 0 rgba(0,0,0,0.12),3px 3px 10px 0 rgba(0,0,0,0.12)}.searchgamecard-text{vertical-align:middle;margin-left:17px;font-weight:600}.searchgamecard-input{box-sizing:border-box;border:none;background-color:rgba(0,0,0,0);color:#EEF7FF;height:40px;width:100%;font-size:1em;font-weight:600;font-family:"Nunito"}.searchgamecard-input:focus{outline:none}.searchgamecard-input::-webkit-input-placeholder{color:rgba(238,247,255,0.3)}.searchgamecard-input:-ms-input-placeholder{color:rgba(238,247,255,0.3)}.searchgamecard-input::-ms-input-placeholder{color:rgba(238,247,255,0.3)}.searchgamecard-input::placeholder{color:rgba(238,247,255,0.3)}

.list-list{width:350px;height:400px;display:inline-block;margin-left:20px;white-space:nowrap}.list-title-card{margin-bottom:30px}.list-card{margin-top:20px}.list-visibility-visible{position:static;margin-top:20px}.list-visibility-hidden{position:absolute;top:-999999px}

