.list-list{
    //background-color: white;
    width: 350px;
    height: 400px;
    display: inline-block;
    margin-left: 20px;
    white-space: nowrap;
}
.list-title-card{
    margin-bottom: 30px;
}
.list-card{
    margin-top: 20px;
}
.list-visibility-visible {
    position: static;
    margin-top: 20px;
}
.list-visibility-hidden {
    position: absolute;
    top: -999999px;
}
